.o-featured-video{
  position: relative;

  &__play{
    @include t-anim--transition(opacity);
    @include t-absolute-center-x-y();
    width: clamp(6.125rem, 20%, 12.25rem);
    cursor: pointer;
    opacity: 0;
    pointer-events: none;

    &.is-visible{
      opacity: 1;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    cursor: pointer;

    &__play{
      width: #{'min(20.13%, 18.5rem)'};
    }
  }
}
