.c-hero-video{
  background-color: $s-color--background-focus;

  &__outer{
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner{
    padding: 3.8125rem $s-gutter--edge-mobile 4rem;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }

  &__preheading{
    margin-bottom: 1rem;
  }

  &__title{
    width: 81%;
    margin-bottom: 1.5rem;
  }

  &__description{
    margin-left: 2.75rem;
    margin-bottom: 1.9375rem;
    max-width: 25.625rem;
  }

  .o-html5-video__video-container,
  &__image-container{
    padding-bottom: 88%;
  }


  /*** Media Queries ***/
  @include t-media-query($s-small-up) {

    &__inner{
      flex-direction: row;
      gap: 1.25rem;
    }

    &__content{
      width: 50%;
    }

    &__video-container{
      width: 50%;
    }

    .o-html5-video__video-container,
    &__image-container{
      padding-bottom: 67.2%;
    }
  }

  @include t-media-query($s-medium-up) {

    &__inner{
      padding: 9.375rem $s-gutter--edge-medium;
    }

    &__content{
      width: 42%;
    }

    &__video-container{
      width: 53.5%;

      .o-featured-video__play{
        width: 18%;
      }
    }
   
    &__title{
      margin-bottom: 2.25rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner{
      padding-top: 14.75rem;
      padding-bottom: 12.1875rem;
      gap: 0;
    }

    &__preheading{
      margin-bottom: 2.875rem;
    }

    &__title{
      margin-bottom: 3.25rem;
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__description{
      margin-left: 6.5rem;
    }

    &__video-container{
      margin-top: 0.4375rem;
    }
  }
}